.movie-wrap {
    display:flex;
    gap:5%;
    max-width:1500px;
    width:100%;
    padding:30px;

    .movie-img {
        width:500px;
        height:750px;
        border-radius:10px;
    }

    .movie-info-wrap {
        width:calc(100% - 500px);

        .movie-title {
            font-size:7rem;
            font-weight:400;
            font-family: 'Anton', sans-serif;
        }

        .movie-date {
            display:flex;
            gap:15px;
            margin-top:30px;

            li {
                font-size:2rem;
                font-weight:400;
                color:#555;
                font-family: 'Anton', sans-serif;
            }
        }

        .movie-ratings {
            div{
                display:flex;
                gap:20px;
                margin-top:0;
                
                .rating-wrap {
                    display:flex;
                    align-items:center;
                    gap:10px;
    
                    img {
                        height:35px;
                    }

                    span {
                        font-weight:600;
                    }
                }
            }
        }

        div { 
            margin-top:30px;
            font-size:1.8rem;

            p {
                line-height:1.5 !important;
            }

            h3{
                font-size:2.8rem;
                font-weight:400;
                font-family: 'Anton', sans-serif;
                margin-bottom:10px;
            }
        }
    }
}